body {
  background-image: url(imgs/bg2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
.heading {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
}

.game-area {
  position: relative;
  margin: 50px auto;
  height: 500px;
  width: 500px;
  /* background-color: rgb(14, 37, 10); */
  border: 2px solid black;
  background-image: url(imgs/bg.jpg);
}

.snake-dot {
  position: absolute;
  width: 3%;
  height: 3%;
  margin: 1px;
  background-color: rgb(214, 150, 22);
  border: 1px solid black;
  border-radius: 5px;
  /* makes snake cover the food  */
  z-index: 2;
}

#snakeHead {
  background-color: rgb(100, 135, 18);
}

#dot1,
#dot2 {
  height: 50%;
  width: 40%;
  background-color: rgb(125, 86, 8);
  border-radius: 50%;

  display: flex;
  margin-left: auto;
}

.snake-food {
  position: absolute;
  width: 3%;
  height: 3%;
  /* margin: 1px; */
  background-color: rgb(225, 58, 36);
  border: 3px solid black;
  border-radius: 10px;
  box-shadow: 2px black;
  z-index: 1;
}
